import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import Swal from 'sweetalert2';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { Router } from '@angular/router';
import { Role } from 'src/app/enum/role.enum';

@Injectable()
export class StatusInterceptor implements HttpInterceptor {

  constructor(
    private authService: AutenticacionService,
    private router: Router
  ) { }

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {

    return next.handle(request)
      .pipe(
        catchError(e => {
          if (e.status == 401) {
            if (this.authService.isUserLoggedIn()) {
              this.authService.logOut();
            }
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Error',
              text:  'Ocurrio un problema',
              icon: 'error'
            });
          }
          if (e.status == 403) {
            if (this.authService.hasRole(Role.ROLE_ESTUDIANTE)) {
              this.router.navigate(['default']);
            } else {
              this.router.navigate(['dash']);
            }
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Advertencia',
              text:  'No tiene permisos para acceder a este recurso',
              icon: 'warning'
            });
          }
          if (e.status == 417) {
            this.authService.logOut();
            window.console.clear()
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Error',
              text:  'Ocurrio un problema',
              icon: 'error'
            });
          }
          return throwError(e);

        })
      );


  }
}
