import { Injectable } from "@angular/core";
import { Credentials, UsuarioA } from "../models/usuario";
import { JwtHelperService } from "@auth0/angular-jwt";
import {
  HttpClient,
  HttpErrorResponse,
  HttpHeaders,
  HttpParams,
  HttpResponse,
} from "@angular/common/http";
import { catchError, map, tap } from "rxjs/operators";
import { HeaderType } from "src/app/enum/header-type.enum";
import { Router } from "@angular/router";
import * as CryptoJS from "crypto-js";
import Swal from "sweetalert2";
import { Role } from "src/app/enum/role.enum";
import { JwtValidationException } from "src/app/core/err/error";
import { Observable, Subject, throwError } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class AutenticacionService {
  user: any;
  cont:number
  private dataSubject = new Subject<UsuarioA>();
  private jwtHelper = new JwtHelperService();
  url = `${environment.urlG}/auth`;
  private token: string;
  private loggedInUsername: string;
  private estado: string;
  encryptionKey: string = "proyectoserviciosocialsindormir";
  showLoading = false;
  sub: string;
  constructor(private http: HttpClient, private router: Router) {
    this.jwtHelper = new JwtHelperService();
    this.loadToken();

  }
  public Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    showCloseButton: true,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener('mouseenter', Swal.stopTimer)
      toast.addEventListener('mouseleave', Swal.resumeTimer)
    }
  })

  login(cred: Credentials) {
    return this.http
      .post(`${this.url}/login`, cred, { observe: "response" })
      .pipe(
        map((response: HttpResponse<any>) => {
          const token = response.body.token;
          this.saveToken(token);
          this.addUserToLocalCache(response.body.usuario);
          if (this.hasRole(Role.ROLE_ESTUDIANTE)) {
            this.router.navigate(["default"]);
          } else {
            this.router.navigate(["dash"]);
          }
        })
      );
  }

  public getToken(): string {
    return this.token;
  }

  public logOut(): void {
    this.token = null;
    this.router.navigate(["account/login"]);
    this.loggedInUsername = null;
    localStorage.removeItem("data");
    localStorage.removeItem("token");
    localStorage.clear();
    sessionStorage.clear();
  }
  public saveToken(token: string): void {
    this.token = token;
    localStorage.setItem("token", token);
  }
  public addUserToLocalCache(user: string) {
    const encryptedUsuario = this.encryptData(user);
    localStorage.setItem("data", encryptedUsuario);
  }

  loadToken(): void {
    this.token = localStorage.getItem("token");
  }

  public getUserFromLocalCache(): any {
    const encryptedUsuarioFromStorage = localStorage.getItem("data");
    const decryptedUsuario = this.decryptData(encryptedUsuarioFromStorage);
    return decryptedUsuario;
  }

  public isUserLoggedIn(): boolean {
    this.loadToken();
    if (this.token != null && this.token !== "") {
      if (this.jwtHelper.decodeToken(this.token).sub != null || "") {
        if (!this.jwtHelper.isTokenExpired(this.token)) {
          this.loggedInUsername = this.jwtHelper.decodeToken(this.token).sub;
          return true;
        }
      }
    } else {
      this.logOut();
      return false;
    }
  }
  hasRoles(roles: string[]): boolean {
    const userRoles = this.getUserFromLocalCache().role;
    if (userRoles == null) return;
    return roles.some((role) => userRoles.includes(role));
  }
  hasRole(role: string): boolean {
    const user = this.getUserFromLocalCache();
    if (user && user.role && user.role.includes(role)) {
      return true;
    }
    return false;
  }
  /*** PARA ENCRIPTAR */
  encryptData(data: any): string {
    const encryptedData = CryptoJS.AES.encrypt(
      JSON.stringify(data),
      this.encryptionKey
    ).toString();
    return encryptedData;
  }
  decryptData(encryptedData: string): any {
    if (!encryptedData) {
      return null;
    }
    try {
      const decryptedData = CryptoJS.AES.decrypt(
          encryptedData,
          this.encryptionKey
      ).toString(CryptoJS.enc.Utf8);
      return JSON.parse(decryptedData);
  } catch (error) {
      this.logOut()
      this.Toast.fire({
        icon: 'error',
        title: 'Ocurrió un problema'
      });
      return null;
  }
  }

  /**RECUPERAR CONTRASEÑA */

  recuperarContrasena(correo: string) {
    const params = new HttpParams().set("correo", correo);
    return this.http.post(`${this.url}/forgotpassword`, {}, { params }).pipe(
      catchError((error) => {
        console.log(error);
        return throwError(error);
      })
    );
  }

  obtenerUser(correo: string) {
    return this.http.get<UsuarioA>(`${this.url}/${correo}`);
  }

  verificarCodigo(id: number, codigo: string) {
    const params = new HttpParams().set("id", id).set("codigo", codigo);
    return this.http.post<UsuarioA>(`${this.url}/expirado`, {}, { params });
  }

  login2(cred: Credentials) {
    return this.http
      .post(`${this.url}/login`, cred, { observe: "response" })
      .pipe(
        map((response: HttpResponse<any>) => {
          const token = response.body.token;
        })
      );
  }
}
