import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError} from 'rxjs';
import { Usuario } from '../models/usuario';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';



@Injectable({
  providedIn: 'root'
})
export class ServiceUsuarioService {

  url = `${environment.urlG}/usuario`;
  urlAuth = `${environment.urlG}/auth`;


  constructor(private http: HttpClient) { }

  usuario:string

  public usuarios(): Observable<Usuario[]> {
    return this.http.get<Usuario[]>(`${this.url}`)
  }

  obtenerUsuarioCorreo(correo: string): Observable<Usuario> {
    return this.http.get<Usuario>(`${this.urlAuth}/${correo}`)
      .pipe(
        catchError(error => {
          console.error("Error en la llamada al servicio: ", error);
          return throwError(error);
        })
      );
  }

  obtenerUsuario(id:number): Observable<Usuario> {
    return this.http.get<Usuario>(`${this.url}/obtener/${id}`)
      .pipe(
        catchError(error => {
          console.error("Error en la llamada al servicio: ", error);
          return throwError(error);
        })
      );
  }


  public usuariosEstado(data:string): Observable<Usuario[]> {
    return this.http.get<Usuario[]>(`${this.url}/estado/${data}`)
  }

  guardar(data: Usuario){
    return this.http.post(`${this.url}`,data)
  }
  editar(data: Usuario){
    return this.http.put(`${this.url}/${data.id}`,data)
  }

  editarContraseña(data: Usuario){
    const params = new HttpParams().set('id', data.id).set('password', data.password);
    return this.http.post<any>(`${this.urlAuth}/password`, {}, { params });
  }

  estado(data: Usuario){
    if(data.estado=="Activo"){
        data.estado="Inactivo";
    }else{
      data.estado="Activo";
    }
    return this.http.put(`${this.url}/${data.id}`,data)
  }
  eliminar(data: Usuario){
    return this.http.delete(`${this.url}/${data.id}`)
  }

  setUsuario(user:string){
    this.usuario=user
  }

  getUsuario(){
    return this.usuario
  }


}
